.form {
    padding: 0 16px;
    padding-top: 23px;
    padding-bottom: 81px;

    @media (min-width: 991px) {
        padding-left: 66px;
        padding-top: 36px;
    }
}

.form__wrapper {
    @media (min-width: 768px) {
        display: flex;
    }
}

.form__title {
    font-weight: 200;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    margin: 0;
    margin-bottom: 23px;

    @media (min-width: 768px) {
        text-align: left;
        margin-bottom: 30px;
    }
}

.form__text {
    font-size: 12px;
    line-height: 126.19%;
    margin: 0;
    margin-bottom: 15px;
    &:last-of-type {
        margin-bottom: 0;
    }

    @media (min-width: 768px) {
        font-size: 14px;
        line-height: 18px;
    }

}

.form__column {
    margin-bottom: 35px;

    @media (min-width: 768px) {
        margin-bottom: 0;
        width: 343px;
        margin-right: 147px;
    }
}