.text-block {
    padding: 27px 20px 33px 31px;
    background-color: #F3F1F1;

    @media (min-width: 992px) {
        width: 401px;
        position: relative;
        z-index: 1;
    }
}

.text-block__title {
    margin: 0;
    margin-bottom: 17px;
    font-size: 18px;
    line-height: 21px;
    font-weight: 100;

    @media (min-width: 768px) {
        font-size: 24px;
        line-height: 28px;
    }
}

.text-block__text {
    line-height: 18px;
    margin: 0;
    margin-bottom: 17px;

    &:last-of-type {
        margin-bottom: 0;
    }
}