.reviews {
    font-family: 'Manrope', Arial, sans-serif;
    margin-top: 33px;
    padding-bottom: 45px;

    @media (min-width: 768px) {
        padding-bottom: 100px;
    }

    @media (min-width: 1152px) {
        margin-top: 121px;
        padding-bottom: 157px;
    }
}

.reviews__wrapper {
    max-width: 854px;
}

.reviews__slide {
    max-width: 854px;
    padding: 0 16px;

    @media (min-width: 768px) {
        padding: 33px;
        padding-top: 0;
    }
}

.reviews__slide-text {
    position: relative;
    font-size: 12px;
    line-height: 21px;
    margin: 0;
    padding-top: 71px;

    &::before {
        content: '';
        position: absolute;
        display: block;
        width: 71px;
        height: 60px;
        top: 0;
        left: 0;
        background-image: url('../img/quote.svg');
        background-repeat: no-repeat;
        background-position: center;
    }

    @media (min-width: 768px) {
        padding-left: 50px;
        padding-top: 0px;
        font-size: 18px;
        line-height: 32px;

        &::before {
            width: 48px;
            height: 98px;
            background-image: url('../img/quote-slide.png');
            top: -25px;
            left: -10px;
        }
    }
}

.reviews__wrapper .swiper-wrapper {
    padding-bottom: 130px;
}

.reviews__wrapper .swiper-pagination {
    bottom: 0;
}

.reviews__wrapper .swiper-pagination-bullet {
    width: 72px;
    height: 72px;
    background-color: transparent;
    opacity: 1;
}

.reviews__wrapper .swiper-pagination-bullet-active,
.reviews__wrapper .swiper-pagination-bullet-main {
    width: 175px;  
    color: #18191F;
}

.reviews__bullet-img {
    display: inline-block;
    vertical-align: middle;
}

.reviews__bullet-name {
    position: absolute;
    font-weight: bold;
    font-size: 18px;
    line-height: 28px;
    white-space: normal;
    top: 0;
    left: 0;
}

.reviews__bullet-text {
    font-size: 14px;
    line-height: 24px;
    white-space: normal;
    position: absolute;
    bottom: 0;
    left: 0;
}

.reviews__bullet-info {
    position: relative;
    width: 95px;
    display: none;
    vertical-align: middle;
    margin-left: 6px;
    white-space: normal;
    padding-top: 30px;
    padding-bottom: 20px;
}

.reviews__wrapper .swiper-pagination-bullet-active .reviews__bullet-info,
.reviews__wrapper .swiper-pagination-bullet-main .reviews__bullet-info {
    display: inline-block;
}

.reviews__wrapper .swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-next-next {
    transform: scale(0.66);
}

@media (min-width: 992px) {
    .reviews__wrapper {

        .swiper-wrapper {
            padding-bottom: 90px;
        }

        .swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-next, 
        .swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-next-next, 
        .swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-prev, 
        .swiper-pagination-bullets-dynamic .swiper-pagination-bullet-active-prev-prev,
        .swiper-pagination-bullets-dynamic .swiper-pagination-bullet {
            transform: scale(1);
        }

        .swiper-pagination-bullet {
            width: 56px;
            height: 56px;
        }

        .swiper-pagination-bullet-active,
        .swiper-pagination-bullet-main {
            width: 175px;  
        }

        .swiper-pagination-bullet-active img,
        .swiper-pagination-bullet-main  img{
            width: 56px;
            height: 56px;
        }
    
        .swiper-pagination {
            width: 854px!important;
        }
    
        &.swiper-container-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet {
            margin: 0 40px;
        }
    
        .swiper-slide {
            // padding: 0;
            box-shadow: 0px 4px 50px 1px rgba(0,0,0,0.05)       
        }
    
        .swiper-slide::before {
            content: '';
            position: absolute;
            display: block;
            width: 100%;
            height: 100%;
            bottom: 0;
            left: 0;
            box-shadow: 0px 4px 50px 1px rgba(0,0,0,0.05);
            z-index: -1;
        }
    
        .swiper-slide-active::after {
            content: '';
            position: absolute;
            display: block;
            bottom: -15px;
            left: 50%;
            transform: translateX(-50%);
            width: 0; 
            height: 0; 
            border-left: 20px solid transparent;
            border-right: 20px solid transparent;
            border-top: 20px solid #ffffff;
            z-index: 2;
        }
    }
}

