.cooperation {
    padding: 22px 16px 141px 16px;
    background-image: url('../img/cooperation-bg.png');
    background-repeat: no-repeat;
    background-position: -400px center;
    background-size: cover;

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
        background-image: url('../img/cooperation-bg@2x.png');
    }

    @media (min-width: 500px) {
        background-position: center;
    }

    @media (min-width: 768px) {
        padding-top: 69px;
        padding-bottom: 97px;
    }
}

.cooperation__wrapper {
    @media (min-width: 768px) {
        padding-right: 115px;
    }
}

.cooperation__text-block {
    @media (min-width: 768px) {
        width: 401px;
        margin-left: auto;
    }

    @media (min-width: 1152px) {
        margin-bottom: 17px;
    }
}

.cooperation__link-wrapper {
    padding: 0 25px 28px 25px;
    background-color: #F3F1F1;

    @media (min-width: 768px) {
        width: 401px;
        margin-left: auto;
    }

    @media (min-width: 1152px) {
        display: flex;
        justify-content: flex-end;
        padding: 0;
        background-color: transparent;
    }
}

.cooperation__link {
    color: #000000;
    font-weight: 400;
    transition: all 0.5s;

    &:hover,
    &:focus {
        text-decoration: none;
        opacity: 0.5;
    }
}
