.main-nav {
    &--opened {
        position: fixed;
        top: 0;
        left: 0;
        width: 320px;
        height: 100vh;
        padding-left: 70px;
        padding-top: 36px;
        padding-bottom: 20px;
        padding-right: 20px;
        background-color: #353535;
        z-index: 2;
        animation: appear 0.5s;
        &::before {
            content: '';
            position: absolute;
            display: block;
            height: 100%;
            width: 44px;
            background-color: #047A33;
            left: 0;
            top: 0;
        }
    }
}

.main-nav .main-nav__wrap {
    display: none;
}

.main-nav--opened .main-nav__wrap {
    display: block;
}

.main-nav__toggle {
    position: absolute;
    top: 12px;
    left: 27px;
    font-weight: 400;
    display: block;
    background-color: transparent;
    border: none;
    cursor: pointer;
    box-shadow: none;
    font-size: 14px;
    line-height: 16px;
    font-weight: 400;
    padding: 0;
    padding-left: 20px;
    outline: none;
    transition: opacity 0.5s;

    &::before {
        top: 3px;
        left: 0;
        content: '';
        display: block;
        position: absolute;
        width: 14px;
        height: 9px;
        background-image: url('../img/toggle-close.svg');
        background-repeat: no-repeat;
        background-position: center;
    }

    &:hover, 
    &:focus {
        opacity: 0.5;
    }

    @media (min-width: 768px) {
        top: 87px;
    }

    @media (min-width: 1152px) {
        top: 99px;
    }
}

.main-nav--opened .main-nav__toggle {
    width: 20px;
    height: 20px;
    top: 10px;
    right: 10px;
    left: auto;
    padding-left: 0;

    &::before {
        width: 20px;
        height: 20px;
        background-image: url('../img/toggle-open-white.svg');
        background-size: 30px;
    }

    span {
        display: none;
    }

    @media (min-width: 768px) {
        top: 5px;
        right: -25px;

        &::before {
            background-image: url('../img/toggle-open-black.svg');
        }
    }
}

.main-nav__wrapper {
    display: flex;
    flex-direction: column;

    &--1,
    &--2,
    &--3 {
        margin-bottom: 30px;
    }

    @media (min-width: 768px) {
        &--1 {
            margin-bottom: 75px;
        }
        &--2 {
            margin-bottom: 64px;
        }

        &--3 {
            margin-bottom: 0;
        }

    }
}

.main-nav__link {
    font-size: 14px;
    line-height: 18px;
    color: #FFFFFF;
    text-decoration: none;
    margin-bottom: 10px;
    transition: opacity 0.5s;
    outline: none;

    &:hover, 
    &:focus {
        opacity: 0.7;
    }

    &:last-of-type {
        margin-bottom: 0;
    }

    @media (min-width: 768px) {
        font-size: 18px;
        line-height: 21px;
        margin-bottom: 27px;
    }
}

.main-nav__list {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    list-style: none;

    @media (min-width: 768px) {
        display: none;
    }
}

@keyframes appear {
    0%   { 
        opacity: 0; 
        left: -320px;
    }
    100% { 
        opacity: 1; 
        left: 0;
    }
  }