.advantages {
    padding-top: 22px;
    padding-bottom: 141px;
    padding-left: 16px;
    padding-right: 16px;
    position: relative;

    @media (min-width: 992px) {
        padding-top: 39px;
        padding-bottom: 72px;
        margin-top: 30px;
    }
}

.advantages__img-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-color: #DBDBDB;
    background-image: url('../img/advantages-img.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
        background-image: url('../img/advantages-img@2x.png');
    }

    @media (min-width: 992px) {
        width: 50%;
        height: calc(100% - 120px);
    }
}

.advantages__wrapper {
    @media (min-width: 992px) {
        margin-left: -50px;
        margin-bottom: 25px;
    }
}

.advantages__column {
    @media (min-width: 992px) {
        width: 50%;
        margin-left: auto;
    }
}

.advantages__link {
    display: block;
    color: #000000;  
    font-weight: 400; 
    transition: opacity 0.5s;

    &:hover, 
    &:focus {
        opacity: 0.5;
    }
}

.advantages__link-wrapper {
    background-color: #F3F1F1;
    padding-bottom: 31px;
    padding-left: 31px;
    padding-right: 20px;

    @media (min-width: 992px) {
        margin-left: 221px;
        background-color: transparent;
        padding-bottom: 0;
        padding-left: 0;
        padding-right: 0;
        margin-bottom: 49px;
    }
}

.advantages__consult-link {
    display: none;
    @media (min-width: 992px) {
        display: block;
        margin-left: auto;
    }
}