.map {
    position: relative;
    min-height: 778px;
    display: flex;

    @media (min-width: 768px) {
        min-height: 465px;
    }
}

.map__iframe {
    position: absolute;
    border: none;
    width: 100%;
    height: 778px;
    top: 0;
    left: 0;

    @media (min-width: 768px) {
        height: 465px;
    }
}

.map__wrapper {
    position: relative;
    z-index: 1;
    width: 100%;
    margin-top: auto;

    @media (min-width: 768px) {
        margin-top: 0;
        height: 100%;
    }
}

.map__contacts-block {
    padding: 27px 21px;
    background-color: rgba(#ffffff, 0.9);

    @media (min-width: 768px) {
        width: 268px;   
        padding: 21px 20px;
    }
}

.map__title {
    font-weight: 300;
    font-size: 18px;
    line-height: 21px;
    margin: 0;
    margin-bottom: 16px;
}

.map__address {
    margin: 0;
    margin-bottom: 31px;
}

.map__link {
    display: block;
    color: #000000;
    margin-bottom: 16px;

    &:last-of-type {
        margin-bottom: 32px;
    }

    &:hover,
    &:focus {
        text-decoration: none;
    }
}

.map__text {
    width: 220px;
    line-height: 21px;
    margin: 0;

    a {
        color: #000000;
        font-weight: 400;
        transition: all 0.5s;

        &:hover,
        &:focus {
            text-decoration: none;
            opacity: 0.5;
        }
    }
}