.page-footer {
    background-color: #000000;
    color: #ffffff;
    padding-top: 36px;
    padding-bottom: 34px;

    @media (min-width: 768px) {
        padding-top: 45px;
        padding-bottom: 39px;
    }

}

.page-footer__wrapper {
    padding: 0 21px;
}

.page-footer__line {
    position: relative;
    padding-top: 50px;
    &::before {
        content: '';
        position: absolute;
        display: block;
        width: 100%;
        height: 1px;
        background-color: #ffffff;
        top: 0;
        left: 0;
        opacity: 0.2;
    }

    @media (min-width: 768px) {
        padding-top: 40px;
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
    }
}

.page-footer__list {
    padding: 0;
    margin: 0;
    list-style: none;
    display: none;
    @media (min-width: 768px) {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin-bottom: 25px;
    }
}

.page-footer__list-item {
    margin-right: 24px;

    &:last-of-type {
        margin-right: 0;
    }
}

.page-footer__list-item a {
    color: #ffffff;
    text-decoration: none;
    line-height: 26px;
    transition: opacity 0.5s;

    &:hover,
    &:focus {
        opacity: 0.7;
    }
}

.page-footer__social {
    padding: 0;
    margin: 0;
    list-style: none;
    display: grid;
    grid-template-columns: repeat(4, 24px);
    grid-column-gap: 16px;
    justify-content: center;
    margin-bottom: 54px;
    @media (min-width: 768px) {
        width: 145px;
        margin-bottom: 0;
    }
}

.page-footer__social-link {
    display: block;
    background-color: rgba(#ffffff, 0.1);
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
    transition: background-color 0.5s;

    &:hover,
    &:focus {
        background-color: rgba(#ffffff, 0.2);
    }

    &--instagram {
        background-image: url('../img/icon-1.svg');
    }

    &--global {
        background-image: url('../img/icon-2.svg');
    }

    &--twitter {
        background-image: url('../img/icon-3.svg');
    }

    &--youtube {
        background-image: url('../img/icon-4.svg');
    }
}

.page-footer__copyright {
    text-align: center;
    font-size: 14px;
    line-height: 24px;
    color: #D9DBE1;
    margin: 0;

    @media (min-width: 768px) {
        text-align: left;
    }
}