.button {
  display: block;
  width: 182px;
  padding: 8px;
  padding-left: 11px;

  font-weight: 400;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  text-shadow: none;
  text-decoration: none;
  border: none;

  background-color: #047A33;;
  border-radius: 13px;

  transition: all 0.5s;

  cursor: pointer;

  &:hover,
  &:focus {
    background-color: #046e2f;
    color: rgba(#ffffff, 0.7);
  }
}