.catalog {
    padding-top: 105px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url('../img/catalog-bg.png');
    background-repeat: no-repeat;
    background-position: top left;
    background-size: 100% 80px;
    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx)  {
        background-image: url('../img/catalog-bg@2x.png');
    }

    @media (min-width: 576px) {
        flex-direction: row;
        flex-wrap: wrap;
    }

    @media (min-width: 768px) {
        padding-top: 20px;
        background-image: none;

        &--with-bg {
            background-image: url('../img/catalog-bg-tablet.png');
            background-position: bottom right;
            background-size: 75% 311px;

            @media (min-resolution: 144dpi), (min-resolution: 1.5dppx)  {
                background-image: url('../img/catalog-bg-tablet@2x.png');
            }
        }
    }

    @media (min-width: 992px) {
        padding-top: 56px;
    }
}

.catalog__title {
    width: 100%;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    margin: 0;
    font-weight: 300;
    margin-bottom: 32px;

    @media (min-width: 768px) {
        position: absolute;
        width: 1px;
        height: 1px;
        margin: -1px;
        border: 0;
        padding: 0;
        white-space: nowrap;
        clip-path: inset(100%);
        clip: rect(0 0 0 0);
        overflow: hidden;
    }
}