.card {
    padding: 14px 20px 28px 17px;
    position: relative;
    display: flex;
    flex-direction: column;
    width: 288px;
    height: 311px;
    background-color: #ffffff;
    background-image: linear-gradient(43.65deg, rgba(255, 255, 255, 0.01) 11.81%, #EFF0F4 121.71%);
    margin-bottom: 10px;

    &:last-of-type {
        margin-bottom: 0;
    }

    @media (min-width: 576px) {
        width: 50%;
        margin-bottom: 0;
    }  

    @media (min-width: 768px) {
        width: 33%;            
    }

    @media (min-width: 992px) {
        width: 25%;
        &--big {
            width: 50%;
        } 
    }
}

.card__title {
    margin: 0;
    margin-bottom: 20px;
    text-transform: uppercase;
    font-weight: 300;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    font-size: 14px;
}

.card__title-link {
    display: block;
    color: #000000;
    text-decoration: none;
    width: 100%;
    height: 100%;
    padding: 14px 20px 45px 17px;
    transition: background-color 0.5s;
    outline: none;

    &:hover,
    &:focus {
        background-color: rgba(0, 0, 0, 0.06);
    }
}

.card__bottom-wrapper {
    margin-top: auto;
    display: flex;
    justify-content: space-between;
}

.card__price {
    margin: 0;
    margin-right: 10px;

    span {
        font-weight: 400;
    }
}

.card__link {
    position: relative;
    display: block;
    color: #000000;
    text-decoration: none;
    padding-left: 19px;
    background-color: transparent;
    border: none;
    z-index: 2;
    transition: opacity 0.5s;
    outline: none;
    text-transform: uppercase;

    &::before {
        content: '';
        display: block;
        position: absolute;
        width: 13px;
        height: 13px;
        top: 1px;
        left: 0;
        background-image: url('../img/bag.png');
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
    }

    &:hover,
    &:focus {
        opacity: 0.5;
    }
}

.card__img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}