.consult-form {
    @media (min-width: 768px) {
        width: 245px;
    }
    
}

.consult-form__wrapper {
    margin: 0;
    margin-bottom: 23px;

    &:last-of-type {
        margin-bottom: 30px;
    }
}

.consult-form__label {
    display: block;
    font-size: 12px;
    line-height: 126.19%;
    margin-bottom: 3px;
}

.consult-form__input {
    font: inherit;
    display: block;
    width: 100%;
    padding: 5px 10px;
    border: 1px solid #000000;
    opacity: 0.3;
    background-color: transparent;
    font-size: 14px;
    line-height: 18px;
    transition: opacity 0.5s;
    outline: none;

    &:hover,
    &:focus {
        opacity: 1;
    }

    &::placeholder {
        font-size: 14px;
        line-height: 18px;
        color: #000000;
    }
}

.consult-form__button {
    width: 108px;
    margin: 0 auto;

    @media (min-width: 768px) {
        margin-right: 0;
    }
}