.quote {
    margin-top: 33px;
    padding: 0 16px;
    padding-bottom: 430px;
    background-image: url('../img/quote-bg.png');
    background-position: bottom right;
    background-repeat: no-repeat;
    background-size: 250px 396px;
    font-family: 'Manrope', Arial, sans-serif;

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
        background-image: url('../img/quote-bg@2x.png');
    }

    @media (min-width: 768px) {
        margin-top: 0;
        padding-top: 115px;
        padding-bottom: 209px;
    }
}

.quote__text {
    margin: 0;
    margin-bottom: 33px;
    position: relative;
    padding-top: 71px;
    font-size: 12px;
    line-height: 21px;
    font-weight: 400;

    &::before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 71px;
        height: 60px;
        background-image: url('../img/quote.svg');
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        z-index: -1;
    }

    @media (min-width: 768px) {
        font-size: 14px;
        line-height: 26px;
        padding-top: 32px;
        padding-left: 49px;
        max-width: 70%;
    }

    @media (min-width: 992px) {
        margin-bottom: 48px;  
        max-width: 730px;
    }
}

.quote__info {
    @media (min-width: 768px) {
        padding-left: 49px;
    }
}

.quote__info-name {
    margin: 0;
    font-weight: 700;
    color: #047A33;

    @media (min-width: 768px) {
        font-size: 18px;
        line-height: 28px;
    }
}

.quote__info-work {
    font-size: 12px;
    line-height: 26px;
    font-weight: 400;

    @media (min-width: 768px) {
        font-size: 14px;
        line-height: 26px;
    }
}